<template>
    <div id="page-content" class="p20 clearfix">
        <div class="panel panel-default">


            <div class="d-flex align-items-center justify-content-between">
                <page-heading />
                <create-button modalHeader='New Purchase Order' modalId="formPurchaseOrderModal">
                    New Purchase Order
                </create-button>
            </div>


            <div class="table-responsive">
                <data-table :table-heading="tableHeading">

                    <template v-slot:filter>
                        <dataFilter  @click="getDataList()"></dataFilter>
                    </template>

                    <template v-slot:data v-if="dataList.purchaseOrders">
                        <tr class="tr" v-for="(purchaseOrder, purchaseOrderIndex) in dataList.purchaseOrders.data" :key="purchaseOrderIndex">
                            <td>{{ serialData(purchaseOrderIndex) }}</td>
                            <td>{{ purchaseOrder.supplier.name }}</td>
                            <td>
                               <span :class="purchaseOrder.status === 'unpaid' ? 'bgc-pink-50 c-pink-700' : 'bgc-green-50 c-green-700'" class="badge  p-10 lh-0 tt-c rounded-pill">
                                   {{ purchaseOrder.status }}
                               </span>
                            </td>
                            <td>{{ purchaseOrder.date }}</td>
                            <td>{{ formatTaka(purchaseOrder.tax) }}</td>
                            <td>{{ formatTaka(purchaseOrder.total_amount) }}</td>
                            <td>
                                <button type="button" class="btn btn-primary btn-color py-1 px-2" title="">
                                    Stock
                                </button>
                            </td>
                            <td class="action">
                                <div class="gap-10 peers d-flex">
                                    <div class="peer">
                                        <eye-button />
                                    </div>
                                    <div class="peer">
                                        <edit-button  @click.native="editData(purchaseOrder, purchaseOrder.id)" />
                                    </div>
                                    <div class="peer">
                                        <delete-button @click.native="deleteInformation(purchaseOrder, purchaseOrder.id)"/>
                                    </div>
                                </div>
                            </td>
                        </tr>
                    </template>

                    <template v-slot:pagination>
                        <div class="datatable-tools clearfix">
                            <div class="col-md-12">
                                Showing {{ dataList.from ? dataList.from : 0 }} To {{ dataList.to ? dataList.to : 0 }}
                                of {{ dataList.total }} entries
                            </div>

                            <div class="col-md-12">
                                <pagination
                                    previousText="PREV"
                                    nextText="NEXT"
                                    :data="dataList"
                                    @paginateTo="getDataList"
                                />
                            </div>
                        </div>
                    </template>
                </data-table>
            </div>
        </div>

        <base-modal
            modal-id="formPurchaseOrderModal"
            modal-size="modal-md"
            modal-title="New Purchase Order"
            @submit="submitForm(formObject, 'formModal')">

            <div>
                <div class="row pb-3">
                    <div class="col-md-4">
                        <label class="form-label required">Supplier</label>
                        <select v-validate="'required'" name="supplier"  class="form-select" v-model="formObject.supplier_id" >
                            <option v-for="supplier in dataList.suppliers" :value="supplier.id" :key="supplier.id">
                                {{ supplier.name }}
                            </option>
                        </select>
                    </div>

                    <div class="col-md-4">
                        <label for="date" class="form-label required">Date</label>
                        <input
                            id="date"
                            name="date"
                            type="date"
                            v-model="formObject.date"
                            placeholder=""
                            class="form-control"
                        />
                    </div>

                    <div class="col-md-4">
                        <label for="tax" class="form-label required">Tax</label>
                        <input
                            id="tax"
                            name="name"
                            type="text"
                            v-model="formObject.tax"
                            placeholder="Tax"
                            class="form-control"
                        />
                    </div>

                </div>

                <div class="row">

                    <div class="col-md-4">
                        <label class="form-label required">Product</label>
                        <select
                            @blur="addToCartProduct()"
                            name="supplier"
                            class="form-select"
                            v-model="formObject.product_id" >
                            <option v-for="product in dataList.products" :value="product.id" :key="product.id">
                                {{ product.item_code }}
                            </option>
                        </select>
                    </div>

                    <div class="col-md-4">
                        <label for="quantity" class="form-label required">Quantity</label>
                        <input
                            id="quantity"
                            name="quantity"
                            type="number"
                            @blur="addToCartProduct()"
                            v-model="formObject.quantity"
                            placeholder=""
                            class="form-control"
                        />
                    </div>

                    <div class="col-md-4">
                        <label for="tax" class="form-label required">Amount</label>
                        <input
                            id="amount"
                            name="name"
                            type="number"
                            @blur="addToCartProduct()"
                            v-model="formObject.amount"
                            placeholder=""
                            class="form-control"
                        />
                    </div>
                </div>
            </div>

            <hr />

            <div class="card" v-if="hasShowingListedProduct">
                <div class="card-body">
                    <table class="display table-light no-footer">
                        <thead>
                            <tr>
                                <th class="text-xl-start" v-for="(th, index) in ['Product', 'Quantity', 'Amount', 'Action']" :key="index">
                                    <small>{{ th }}</small>
                                </th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(data, index) in formObject.products" :key="index">
                                <td>
                                    <select
                                        name="supplier"
                                        class="form-select"
                                        v-model="formObject.product_id"
                                        @change="changeItem()">
                                        <option v-for="product in dataList.products"
                                                :value="product.id"
                                                :key="product.id"
                                                :selected="product.id === formObject.product_id">
                                            {{ product.item_code }}
                                        </option>
                                    </select>
                                </td>
                                <td>
                                    <input
                                        name="item_quantity"
                                        type="number"
                                        v-model="data.quantity"
                                        class="form-control" readonly
                                    />
                                </td>
                                <td>
                                    <input
                                        name="item_amount"
                                        type="number"
                                        v-model="data.amount"
                                        class="form-control"
                                        readonly
                                    />
                                </td>
                                <td>
                                    <delete-button @click.native="(index) => formObject.products.splice(index,1)" />
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </base-modal>
    </div>
</template>

<script>
export default {
    data() {
        return {
            tableHeading: {
                sl: "Sl",
                supplier_id: "Supplier",
                status: "Status",
                date: "Date",
                tax: "Tax",
                total_amount: "Total Amount",
                stock: 'Add Stock',
                action: "Action",
            },
        };
    },

    computed: {
      hasShowingListedProduct() {
          if (!this.formObject.products || this.formObject.products.length > 0) {
              return true
          }
          return false
      },

      totalAmount() {
          this.formObject.products.forEach(item => {
              console.log(item)
          })
      }
    },

    methods: {
        resetFormData() {
            this.$set(this.formObject, "item_code", "");
            this.$set(this.formObject, "quantity", "");
            this.$set(this.formObject, "amount", "");
            this.$set(this.formObject, "total_amount", "");
            this.$set(this.formObject, "supplier_id", "");
            this.$set(this.formObject, "date", "");
            this.$set(this.formObject, "tax", "");
        },

        addToCartProduct() {

            if (!this.formObject.product_id || !this.formObject.quantity || !this.formObject.amount) {
                return false
            }

            this.formObject.products.push({
                product_id: this.formObject.product_id,
                quantity: this.formObject.quantity,
                amount: this.formObject.amount
            });

            this.$set(this.formObject, "product_id", '');
            this.$set(this.formObject, "quantity", '');
            this.$set(this.formObject, "amount", '');
        }
    },

    mounted() {
        this.resetFormData();
        this.getDataList();
        this.$set(this.formObject, "products", []);
    },
};
</script>

<style lang="scss" scoped>
.tr {
    td {
        width: 10%;
    }
}
.action{
    width: 12% !important;
}
</style>
